import {
    request
} from '../utils/request'
import { baseUrl, uapUrl } from './baseUrl'
import axios from 'axios'
export function getImage() { //曲线分析
    return request({
        method: 'get',
        url: `${baseUrl}/captchaImage`,
    })
}


export function login(data) { //登录
    return request({
        method: 'post',
        url: `${uapUrl}/api/login/loginWeb`,
        // url: `${uapUrl}/api/login/loginWeb`,
        data
    })
}

//获取用户角色权限
export function getInfo(userid) {
    return request({
        url: `${uapUrl}/roleApi/listByUser.api?userId=${userid}`,
        method: 'post',
    })
}


export function logout() {
    return
    // return request({
    //     url: `${baseUrl}/getInfo`,
    //     method: 'get'
    // })
}

export function getDetail(id) {
    return request({
        url: 'wb/user/detail?userId=' + id,
        method: 'get'
    })
}
/**
 * 读取版本号
 * @returns {String} 
 */
export function getVersion() {
    return axios.get("/version.json", {})
}