
import { constantRoutes, operationRoutes, wellRoutes, financeRoutes } from '@/router' //获取默认的运维人员菜单 和井队人员菜单
import router from '@/router'

import { ROLENAME } from "@/utils/config.js";

function filterAsyncRouter(routes, name) {  //过滤角色
    var data = routes.filter(route => {
        return route.meta.roles.length == 0 || route.meta && route.meta.roles && name.some(v => route.meta.roles.includes(v))
    })
    return data;
}
const state = {
    routes: null,// 权限菜单
    menuItems: null,// 左侧菜单树
    roles: '',// 用户的权限数组
}
const getters = {
    get_routes: state => state.routes,
}

//把三级菜单转换为二级菜单路由
const formatRouter = (routers, newRoutes = []) => {
    routers.map(item => {//1级菜单
        const newItem = { ...item }//拷贝一份数据再操作否则会影响原数组
        if (newItem.children && newItem.children.length > 0) {
            let threeLiveData = [];//三级菜单子集集合
            newItem.children.map(lis => {//2级菜单
                if (lis.children && lis.children.length > 0) {
                    threeLiveData.push({
                        data: lis.children,
                        item: lis,
                    })
                }
            })
            //把所有三级菜单转为二级菜单
            if (threeLiveData.length) {
                threeLiveData.map(obj => {
                    newItem.children = newItem.children.filter(n => n !== obj.item);
                    newItem.children = newItem.children.concat(obj.data)
                })
            }
        }
        newRoutes.push(newItem)
    })
    return newRoutes;
}


const actions = {
    GENERATEROUTES({ commit }, roles) {
        //添加路由
        let routers;
        if (roles.includes(ROLENAME.yw)) {//运维
            //运维人员没有电费收缴
            operationRoutes[2].children.splice(2, 1)
            operationRoutes.splice(5, 1);
            routers = constantRoutes.concat(operationRoutes);
        } else if (roles.includes(ROLENAME.marketer)) {
            operationRoutes[2].children.shift(); //市场人员没有抄表读数
            operationRoutes[3].children.shift(); //市场人员没有计量点管理
            operationRoutes.splice(4, 1); //市场人员没有用电管理
            // operationRoutes.splice(1, 1); //市场人员没有电量分析
            routers = constantRoutes.concat(operationRoutes);
        } else if (roles.includes(ROLENAME.well)) {//井队人员
            routers = constantRoutes.concat(wellRoutes)
        } else if (roles.includes(ROLENAME.finance)) {//财务人员
            routers = constantRoutes.concat(financeRoutes)
        } else {
            routers = constantRoutes.concat(wellRoutes)
        }
        commit('SET_MENUS', routers.filter(item => { // 去除基本的路由 即为菜单树
            return !constantRoutes.find(n => item.path === n.path)
        }));

        commit('SET_ROUTES', routers);
        commit('SET_ROLES', roles);
        let flatRoutes = formatRouter(routers);
        router.addRoutes(flatRoutes);
        /*  动态添加路由 */
    },
}

const mutations = {
    SET_ROUTES: (state, d) => {
        state.routes = d;
    },
    SET_ROLES: (state, d) => {
        state.roles = d;
    },
    SET_MENUS: (state, d) => {
        state.menuItems = d;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}
