import {
    login,
    getInfo,
    getDetail,
} from '@/api/login'

import {
    postRestPwd
} from '@/api/archives'

import {
    getPoints,
    changePoint
} from '@/api/home'
import {
    getToken,
    setToken,
    removeTokenAll,
    setLocalStorageToken
} from '@/utils/auth'
const user = {
    state: {
        token: getToken(),
        userId: getToken('userid'),
        jp_code: '',
        work_type: '', //用电性质
        team: '', //井队名称
        teamShortName: '', //平台名称
        points: [],//计量点
        kfname: '',
        kfphone: '',
        roles: '',
        name: '',
        nickName: '',
    },

    mutations: {
        SET_TOKEN: (state, token) => {
            state.token = token;
        },
        SET_NAME: (state, name) => {
            state.name = name;
        },
        SET_NICK_NAME: (state, name) => {
            state.nickName = name;
        },
        SET_AVATAR: (state, avatar) => {
            state.avatar = avatar;
        },
        SET_ROLES: (state, roles) => {
            state.roles = roles;
        },
        SET_PERMISSIONS: (state, permissions) => {
            state.permissions = permissions;
        },
        SET_USERID: (state, userId) => {
            state.userId = userId;
        },
        SET_HOMEPATH: (state, path) => {
            state.homePath = path;
        },
        SET_DEPTID: (state, deptId) => {
            state.deptId = deptId;
        },
        SET_JPCODE: (state, jp_code) => {
            state.jp_code = jp_code;
        },
        SET_INFO: (state, work) => {
            let newwork = work.work ? work.work : {};
            let kf = work.kf ? work.kf : {};
            state.work_type = newwork.work_type;
            state.team = newwork.team;
            state.teamShortName = newwork.teamShortName;
            state.kfname = kf.kfname;
            state.kfphone = kf.kfphone;
        },
        SET_POINTS: (state, points) => {// 获取计量点
            state.points = points
        },
        CHANGE_POINTS: (state, work) => {// 切换计量点
            let newwork = work.work ? work.work : {};
            let kf = work.kf ? work.kf : {};
            state.work_type = newwork.work_type;
            state.team = newwork.team;
            state.teamShortName = newwork.teamShortName;
            state.jp_code = newwork.jp_code;
            state.kfname = kf.kfname;
            state.kfphone = kf.kfphone;
        },
    },

    actions: {
        // 登录
        Login({
            commit, dispatch, state
        }, userInfo) {
            userInfo.userName.trim();
            return login(userInfo).then(res => {
                let user = res.data.user;
                setToken('accessToken', res.data.accessToken);
                setToken('refreshToken', res.data.refreshToken);
                setToken('userid', user.id);
                //储存在本地
                setLocalStorageToken('accessToken', res.data.accessToken);
                setLocalStorageToken('refreshToken', res.data.refreshToken);
                setLocalStorageToken('userid', user.id);

                commit('SET_TOKEN', res.data.accessToken);
                commit('SET_USERID', res.data.user.id);
                commit('SET_NAME', user.userName);
                commit('SET_NICK_NAME', user.realName);
                //获取角色权限
                return dispatch('GetInfo');
            })
        },
        // 获取用户角色
        GetInfo({
            commit,
            dispatch,
            state
        }) {
            return getInfo(state.userId).then(res => { //获取用户信息
                //获取权限数组 只取名称
                const roles = res.data.map(p => p.roleName);
                if (roles && roles.length > 0) { // 验证返回的roles是否是一个非空数组
                    commit('SET_ROLES', roles)
                    commit('SET_PERMISSIONS', res.permissions);
                    //加入菜单路由
                    dispatch('GENERATEROUTES', roles)
                } else {
                    commit('SET_ROLES', ['ROLE_DEFAULT'])
                }
                return res;
            })
        },
        //通过userid获取当前用户的详细信息
        GetDetail({
            commit,
            state
        }) {
            return getDetail(state.userId).then(res => { //获取用户信息
                const work = res.work;
                commit('SET_INFO', res);
                if (res.work) {
                    commit('SET_JPCODE', work.jp_code)
                    commit('SET_USERID', work.user_id);
                    commit('SET_NAME', work.nick_name);
                    commit('SET_NICK_NAME', work.nick_name);
                }
            }).catch(err => {
                console.error('获取用户信息错误:/user/detail', err);
            })
        },

        GetPoints({
            commit,
            state
        }) {
            return getPoints(state.userId).then(res => { //获取用户管辖的计量点信息
                commit('SET_POINTS', res.data);
                const curr = res.data.find(item => item.alive == 1);
                curr && commit('SET_JPCODE', curr.code);
                return res;
            })
        },
        changPoint({
            commit,
            state
        }, data) {
            let query = {
                userId: state.userId,
                jpcode: data.jpcode
            }
            return changePoint(query).then(res => {
                commit('CHANGE_POINTS', res)
            })
        },

        // 退出系统
        LogOut({
            commit,
            state,
        }) {
            return new Promise((resolve, reject) => {
                commit('SET_TOKEN', '')
                commit('SET_ROUTES', '')
                commit('SET_ROLES', [])
                removeTokenAll()
                resolve();
            })
        },
        // 修改密码
        ResetPwd({
            commit,
        }, data) {
            return postRestPwd({
                ...data,
                uapAccessToken: getToken('accessToken'),
                uapRefreshToken: getToken('refreshToken')
            }).then(res => {
                commit('SET_TOKEN', '')
                removeTokenAll()
                return;
            })
        },
        // 前端 登出
        FedLogOut({
            commit
        }) {
            return new Promise(resolve => {
                commit('SET_TOKEN', '')
                removeTokenAll()
                resolve()
            })
        }
    }
}

export default user