// 日期格式化
export function parseTime(time, pattern) {
    if (arguments.length === 0 || !time) {
        return null
    }
    const format = pattern || '{y}-{m}-{d} {h}:{i}:{s}'
    let date
    if (typeof time === 'object') {
        date = time
    } else {
        if ((typeof time === 'string') && (/^[0-9]+$/.test(time))) {
            time = parseInt(time)
        } else if (typeof time === 'string') {
            time = time.replace(new RegExp(/-/gm), '/')
        }
        if ((typeof time === 'number') && (time.toString().length === 10)) {
            time = time * 1000
        }
        date = new Date(time)
    }
    const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay()
    }
    const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
        let value = formatObj[key]
        // Note: getDay() returns 0 on Sunday
        if (key === 'a') { return ['日', '一', '二', '三', '四', '五', '六'][value] }
        if (result.length > 0 && value < 10) {
            value = '0' + value
        }
        return value || 0
    })
    return time_str
}
export function formatDate(cellValue) {
    if (cellValue == null || cellValue === '') return ''
    var date = new Date(cellValue)
    var year = date.getFullYear()
    var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
    var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
    var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
    var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
    var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
    return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes + ':' + seconds
}

/**
 * @param {number} time
 * @param {string} option
 * @returns {string}
 */
export function formatTime(time, option) {
    if (('' + time).length === 10) {
        time = parseInt(time) * 1000
    } else {
        time = +time
    }
    const d = new Date(time)
    const now = Date.now()

    const diff = (now - d) / 1000

    if (diff < 30) {
        return '刚刚'
    } else if (diff < 3600) {
        // less 1 hour
        return Math.ceil(diff / 60) + '分钟前'
    } else if (diff < 3600 * 24) {
        return Math.ceil(diff / 3600) + '小时前'
    } else if (diff < 3600 * 24 * 2) {
        return '1天前'
    }
    if (option) {
        return parseTime(time, option)
    } else {
        return (
            d.getMonth() +
            1 +
            '月' +
            d.getDate() +
            '日' +
            d.getHours() +
            '时' +
            d.getMinutes() +
            '分'
        )
    }
}

/**
 * 获取上个账单月，如果没超过25号则返回上一月25号，超过则返回本月25号
 * @param {number} day 出账单日默认自然月的25号
 * @returns {Date}
 */
export function getPrevMonth(day = 25, now = new Date()) {
    const date = now.getDate();
    if (date < day) {//返回上一个月
        now.setMonth(now.getMonth() - 1);
        now.setDate(day);
        return now;
    }
    //超过day
    now.setDate(day);
    return now;
}

/**
 * 转换数字 保留2位小数 如果是 1.00或1.0 则返回整数1  如果是1.10 返回 1.1
 * @param {number | string }
 * @returns {string}
 */
export function formatNum(num, len = 2) {
    if (!num) return 0;
    num = Number(num).toFixed(len);
    return Number(num);
}


/**
 * 转换金额数字，整数部分三位数一个逗号 例如  11110000 => 2,311,110,000 
 * 小数部分不处理
 * @param {number | string } money
 * @param {number } n 保留小数位数默认2位
 * @returns {string}
 */
export function formatMoneyAddComma(money, n = 2) {
    if (money == '' || money == undefined || money.toString() == 'NaN' || money == Infinity || money != Number(money)) {
        return 0;
    }
    money = (money * 1).toFixed(n);
    let [left, right] = money.split('.');
    //如果小数位数不够小数点后面补0，默认2位小数
    const fh = left.startsWith('-');
    const intLen = fh ? left.length - 1 : left.length;
    const flagNumber = Math.floor((intLen) / 3)
    for (let i = 1; i <= flagNumber; i++) {
        let cutindex = -3 * i - (i - 1);
        left = left.slice(0, cutindex) + ',' + left.slice(cutindex)
    }
    if (left.startsWith(',')) left = left.slice(1);
    let res = left + (right ? ('.' + right) : '');
    if (Number(money) == 0) return '0';
    return res;
}

/**
 * 获取数字的最大小数位数，
 * @param {Array | string } arr
 * @returns {Number}
 */
export function getMaxFixedLen(number) {
    if (!number) return 0;
    if (!number.length) {
        number = [number]
    }
    let max = 0;
    number.map(n => {
        const str = n + '';
        let l = str.split('.')[1];
        if (!l) return;
        let k = l.length;
        if (k > max) max = k;
    })
    return max;
}


/**
 * 获取一个日期的凌晨0点时间
 * @param {Date } date
 * @returns {Date }
 */
export function getDayStart(date = new Date()) {
    date = new Date(date);
    date.setHours(0);
    date.setMinutes(0);
    date.setSeconds(0);
    return date;
}

/**
 * 获取上月某号到本月某号的一个时间段
 * @param { Number } dayNumber 1-29号的时间 默认26号
 * @returns { Array }
 */
export function getDefaultTimeRange(dayNumber = 26) {
    let now = new Date();
    let currMonth = now.getMonth();
    let pervMonth = currMonth - 1;
    let prevTime = new Date(now.getTime());
    prevTime.setMonth(pervMonth);
    prevTime.setDate(dayNumber);
    now.setDate(dayNumber);
    return [getDayStart(prevTime), getDayStart(now)]
}


/**
 * 把一个时间范围按照差值拆分成数组，展示的值可以传入format格式输出
 * @param { Array } range 时间范围 [startDate,endDate]
 * @param { Number } step 每个间隔的时间戳时长  默认一天
 * @param { String } format 转换格式 默认 {y}-{m}-{d}
 */
export function getTimeRangeArr(range, step = 24 * 60 * 60 * 1000, format = '{y}-{m}-{d} {h}:{i}:{s}') {
    let arr = [];
    if (!range || !range[0] || !range[1]) return arr;
    let sDate = new Date(range[0].getTime());
    let eDate = new Date(range[1].getTime());

    do {
        arr.push(parseTime(sDate, format));
    } while (sDate.setMilliseconds(sDate.getMilliseconds() + step) <= eDate)

    return arr;
}


/**
 * 返回某一天的起止时间段,默认获取当天凌晨到次日凌晨
 * @example
 * daySE()
 * //[Fri Sep 03 2021 00:00:00 GMT+0800 (中国标准时间), Sat Sep 04 2021 00:00:00 GMT+0800 (中国标准时间)]
 * @example
 * daySE(new Date(),false)
 * //[Fri Sep 03 2021 00:00:00 GMT+0800 (中国标准时间), Fri Sep 03 2021 23:59:59 GMT+0800 (中国标准时间)]
 * @example
 * daySE(new Date('2021/2/13 14:00:00'))
 * //[Sat Feb 13 2021 00:00:00 GMT+0800 (中国标准时间), Sun Feb 14 2021 00:00:00 GMT+0800 (中国标准时间)]
 * @param {Date} date 待转换时间
 * @returns {Array}
 */
export function daySE(date = new Date(), add = true) {
    if (date.constructor.name != 'Date') {
        date = new Date(date);
    }
    const s = new Date('2021/1/1 00:00:00');
    const e = new Date('2021/1/1 00:00:00');
    s.setFullYear(date.getFullYear());
    s.setMonth(date.getMonth());
    s.setDate(date.getDate());
    e.setFullYear(date.getFullYear());
    e.setMonth(date.getMonth());
    e.setDate(date.getDate() + 1);
    if (add) {
        return [s, e];
    } else {
        e.setMilliseconds(e.getMilliseconds() - 1);
    }
    return [s, e];
}

/**
 * 防抖函数
 * @param {}} callback 
 * @param {*} interval 
 * @returns 
 */
export function debounce(callback, interval) {
    let debounceTimeoutId;

    return function (...args) {
        clearTimeout(debounceTimeoutId);
        debounceTimeoutId = setTimeout(() => callback.apply(this, args), interval);
    };
}