import Cookies from 'js-cookie'


export function getToken(type = "accessToken") {
    return Cookies.get(type) ? Cookies.get(type) : getLocalStorageToken(type)
}

export function setToken(type, token) {
    Cookies.set(type, token)
}

//localstorge get
export function getLocalStorageToken(type) {
    return localStorage.getItem(type);
}
//localstorge set
export function setLocalStorageToken(type, token) {
    localStorage.setItem(type, token);
}


export function removeToken(type = "accessToken") {
    Cookies.remove(type);
    localStorage.removeItem(type);
}

export function removeTokenAll() {
    Cookies.remove('accessToken');
    Cookies.remove('userid');
    Cookies.remove('refreshToken')

    Cookies.remove('uapAccessToken', { path: '', domain: '.minexiot.com' });
    Cookies.remove('uapUserId', { path: '', domain: '.minexiot.com' });
    Cookies.remove('uapRefreshToken', { path: '', domain: '.minexiot.com' })

    localStorage.removeItem('accessToken');
    localStorage.removeItem('userid');
    localStorage.removeItem('refreshToken');
}

const uapAccessToken = getToken("uapAccessToken");
const uapRefreshToken = getToken("uapRefreshToken");
const uapUserId = getToken("uapUserId");

//判断是否是uap权限系统跳转过来
if (uapAccessToken) {
    setToken('accessToken', uapAccessToken);
    setToken('refreshToken', uapRefreshToken);
    setToken('userid', uapUserId);
    //储存在本地
    setLocalStorageToken('accessToken', uapAccessToken);
    setLocalStorageToken('refreshToken', uapRefreshToken);
    setLocalStorageToken('userid', uapUserId);
}